* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: black;
}
.cal {
  border: 4px solid rgb(36, 36, 36);
  border-radius: 10px;
}
button {
  width: 7vw;
  border: none;
  height: 10vh;
  margin: 2px;
  border-radius: 30px;
  color: white;
  background-color: rgb(56, 56, 56);
  font-size: 20px;
}
.op {
  color: yellowgreen;
}
#equal {
  background-color: yellowgreen;
}
.clc {
  color: tomato;
}
.result {
  position: relative;
  left: 2px;
  height: 15vh;
  margin: 4px;
  width: 28.5vw;
  border: 2px solid black;
  border-bottom: 1px solid white;
  padding: 7vh 23px;
  color: white;
  text-align: right;
  font-weight: 300;
  font-size: 30px;
}
@media screen and (max-width: 480px) {
  .button {
    position: relative;
    left: 2vw;
    bottom: 2px;
  }
  .result {
    position: relative;
    left: 2vw;
    height: 16vh;
    width: 90vw;
    border: 2px solid black;
    border-bottom: 1px solid white;
    margin: 4px;
  }
  button {
    width: 23vw;
    height: 14vh;
    margin: 2px;
  }
  body {
    justify-content: left;
  }
}
